
import { defineComponent } from 'vue';
import Header from "../components/Header.vue"
import Footer from "../components/Footer.vue"
import Verifier from "../components/Verifier.vue"
import Tutorial from "../components/Tutorial.vue"

export default defineComponent({
  name: 'SfAbstaende',
  components: {
    Header,
    Footer,
    Verifier,
    Tutorial
  },
  data() {
    return {
      showTutorial: false,
      numbers: [] as number[][],
      anz_tage: 3,
      answer: 0,
      submitted: false as boolean,
      result: "falsch.",
      ans_abst: [false,false,false,false,false],
      falsch_beantwortet: [true,true,true,true,true],
      answered: false,
    }
  },
  created: function(){
    this.createNumbers()
  },
  props: {
    msg: String
  },
  methods : {
    reloadPage(){
      this.$router.go(0)
    },
    hint(){
      if(!this.answered){
        return "Bitte wähle eine Zahl aus."
      }
    },
    reset_all(){
      this.deactivate_all_btns()
      this.answered = false
      this.submitted = false
      this.answer = -1
      this.result = "falsch."
    },
    compute_abstand(){
      var sum = 0
      for(let i = 0; i < this.numbers[0].length; i++){
        if(this.numbers[0][i] != this.numbers[1][i]){
          sum++
        }
      }
      return sum
    },
    deactivate_all_btns(){
      for(let i = 0; i < 5; i++){
        this.answered = true
        document.getElementById('btn_'+i)!.style.backgroundColor = "#ffe5b2"
        document.getElementById('btn_'+i)!.style.borderWidth="thin"
        document.getElementById('btn_'+i)!.style.fontWeight="400"
        this.ans_abst[i] = false
      }
    },
    answer_abstand(zahl: number){
      if(zahl >= 0 && zahl <= 4){
        this.deactivate_all_btns()
        this.ans_abst[zahl] = true;
        document.getElementById("btn_"+zahl)!.style.backgroundColor = "#e1975a"
        document.getElementById("btn_"+zahl)!.style.borderWidth="medium"
        document.getElementById("btn_"+zahl)!.style.fontWeight="700"
        this.answer = zahl+1
      }
    },
    submitAnswer(){
      /* TODO */
      this.submitted = true
      this.checkAnswer()
    },
    checkAnswer(){
      var num_diff = 0
      for(let i = 0; i < this.numbers[0].length; i++){
        if(this.numbers[0][i] != this.numbers[1][i]){
          num_diff++
        }
      }

      if(num_diff == this.answer){
        this.result = "korrekt.";
      } else {
        this.result = "falsch."
      }
    },
    
    createNumbers(){
      //zuerst wollen wir das array "numbers" befüllen
      let new_array_o: number[][]
        console.log('duplikat gefunden, codes neu generiert')
        console.log(this.numbers)
        new_array_o = []
        for(let i = 0; i < 2; i++){
          let new_array = []
          for(let i = 0; i < 5; i++){
            new_array.push(Math.round(Math.random()))
          }
          new_array_o.push(new_array)
        }
        this.numbers = new_array_o
    },
    task_description(){
      return "Bei dieser Aufgabe musst du herausfinden, an welchen Stellen sich die beiden unteren Codes unterscheiden. Klicke dafür "+
        "auf den jeweiligen Pfeil. Bei der schwierigeren Aufgabe (wenn du auf die 3 Biber oder auf \"Nächste Aufgabe\" klickst), "+
        "musst du angeben, an wie vielen Stellen sich die beiden Codes unterscheiden. Klicke dafür auf eine der Zahlen."
    }
  }
});
